.App .App-Burger {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 1;
  -webkit-transition: 400ms opacity;
  -o-transition: 400ms opacity;
  transition: 400ms opacity; }

.App .SideMenu-wrapper {
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  position: absolute; }
  .App .SideMenu-wrapper .SideMenu {
    -webkit-transition: 400ms opacity, 400ms -webkit-filter;
    transition: 400ms opacity, 400ms -webkit-filter;
    -o-transition: 400ms opacity, 400ms filter;
    transition: 400ms opacity, 400ms filter;
    transition: 400ms opacity, 400ms filter, 400ms -webkit-filter; }

.App .view-wrapper {
  -webkit-transition: opacity 300ms, -webkit-filter 300ms;
  transition: opacity 300ms, -webkit-filter 300ms;
  -o-transition: filter 300ms, opacity 300ms;
  transition: filter 300ms, opacity 300ms;
  transition: filter 300ms, opacity 300ms, -webkit-filter 300ms;
  opacity: 1;
  height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch; }

.App.menu-active .App-Burger {
  opacity: 0; }

.App.menu-active .view-wrapper {
  -webkit-filter: blur(15px);
          filter: blur(15px);
  opacity: .3;
  overflow-y: hidden; }

.App.menu-active .SideMenu-wrapper {
  z-index: 4; }

.App.full-image-active .App-Burger {
  opacity: 0; }

@media (min-width: 992px) {
  .App .SideMenu-wrapper {
    width: 0;
    min-height: 0;
    overflow: auto;
    position: inherit; }
  .App .view-wrapper {
    padding: 0 300px 0 0; }
  .App.menu-active .view-wrapper {
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
    overflow-y: scroll; }
  .App.menu-active .SideMenu-wrapper {
    z-index: auto; }
  .App.full-image-active .SideMenu-wrapper .SideMenu {
    opacity: .7;
    -webkit-filter: blur(5px);
            filter: blur(5px); } }



